import React from 'react'
import {Link} from 'react-router-dom'

// 
import './ActSuc.scss'

// 
import SucHeader from './images/SucHeader.png'

// 
import Share from './Share/Share'

class ActSuc extends React.Component{
    share(){
        console.log(this.refs.childRef.refs.ShareW);
        this.refs.childRef.refs.ShareW.style.display = 'block';
    }
    render(){
        return(
            <div className="ActSucWrap">
                
                {/** */}
                <header className="ActSucHeaderWrap">
                    <img src={SucHeader} width="100%"/>
                    <div className="SucInfo">
                        <span className="SucInfoTxt">  </span>
                    </div>
                </header>

                {/** */}
                <main className="SucImgWrap">
                    <div className="SucImgMain">
                    <div className="SucImgMain">

                    <div className="SucImgTil">
                        
                    </div>

                    <div className="SucImg">

                    </div>

                    <div className="SucBtn">
                        <span className="SucB" onClick={this.share.bind(this)}>
                        </span>
                    </div>

                </div>
                    </div>
                </main>


                <Share ref="childRef"></Share>
            </div>
        )
    }
}
export default ActSuc