import React from 'react'
// const QRcode  = require('qrcode.react');
import {Link} from 'react-router-dom'
import QRcode from 'qrcode.react'
// import './canvas'
import $ from 'jquery'

import {getBase64,getCookie , SAVELOCAL} from "./../Methods/Methods"


import './Active.scss'

// 
import ActTit from './images/ActiveTitle.png'
import ActUser from './images/ActiveUser.png' 
import ActiveImg from './images/ActiveImg.png'
import ActBtn from './images/ActBtn.png'
import saveImg from './images/saveImg.png'
import Act_Suc from './images/Act_Suc.png'
import Act_Suc_Close from './images/Act_Suc_Close.png'

// 
import {urlImg , getUserInfomationr , userAssisted , receiveGiftOrder , ActiveName} from './../apis/api'

// 

import Loading from './../Components/Loading/Loading';
import Tips from './../Components/Tips/Tips';


class Active extends React.Component{
    constructor(props){
        super(props)
        this.state={
            userName : "",
            url: "",
            liked : false,
            midBase : "",
            isMe : "",
            isYou : "",
            ActProTil : true,
            isAct : true,
            isCount :0, 
            baseData:""//base64
        }
            
        
    }
    componentDidMount(){
        var storage = window.localStorage
        var url = this.props.location.pathname
        // console.log(this.props.location.pathname);
        var key = window.location.hash.substr(9)
        var keys = storage.keys
        var be_mid = storage.be_mid
        var _this = this

        

        // 
        if(key){
            this.setState({
                ActProTil : false,
                isYou : true
            })
            this.refs.ActPay.style.display = "none"
            this.refs.ActPayBck.style.height = "6.5rem"
            SAVELOCAL('ActPath' , url)


            // ---
            if(!be_mid){
                // console.log("");
                _this.props.history.push('/Login')
                
            }

            // ---
            else{
                // console.log('');
                var storage = window.localStorage
                storage.removeItem("ActPath")


                // 
                this.getUser = $.get(getUserInfomationr , {keys : key}, function(res){
                    if(res.msg === "SUCCESS"){
                        if(res.data.nickname){
                            _this.setState({
                                isCount : res.data.count,
                                userName : res.data.nickname
                            })
                        }
                        if(_this.refs.userImg){
                            _this.refs.userImg.style.background = "url(" + res.data.avatar + ")"
                            _this.refs.userImg.style.backgroundSize = "100% 100%"
                        }
                        if(res.data.count >= 2){
                            var This = _this
                            
                            _this.setState({
                                // isYou : false,
                                isAct : false,
                                TipsInfo : "!"
                            })
                            _this.refs.ActBtn.innerHTML = ""
            
                            _this.timerItem1 = setTimeout(function(){
                                This.refs.childRefTwo.refs.Tips.style.top = '0%'
                            },1000)
                            _this.timerItem2 = setTimeout(function(){
                                This.refs.childRefTwo.refs.Tips.style.top = '-40%'
                            },3000)
                        }
                        if(res.data.count >= 1){
                            _this.refs.ActiveProCri.style.boxShadow = "0px 0px  20px 7px rgb(255,255,0)"
                        }
                    }
                }) 
            }
        }


        // 
        else{
            // console.log("")
            this.setState({
                isMe : true
            })
            if(storage.count >= 2){
                // console.log('');
                // console.log(this.refs.ActBtn.innerHTML)
                this.setState({
                    isMe : false
                })
            }

            // 
            this.getUserItem = $.get(getUserInfomationr,{keys : keys } , function(res){
                // console.log(res);
                if(res.msg === "SUCCESS"){
                    SAVELOCAL('count' , res.data.count)
                    _this.refs.userImg.style.background = "url(" + res.data.avatar + ")"
                    _this.refs.userImg.style.backgroundSize = "100% 100%"
                    _this.setState({
                        userName : res.data.nickname,
                        isCount : res.data.count,
                        midBase : keys
                    })
                    // console.log(_this.state.midBase);
                    var codeUrl = window.location.href +  "/" + _this.state.midBase
                    _this.setState({
                        url : codeUrl
                    })
                    // console.log(codeUrl)
                    if(res.data.count >= 1){
                        _this.refs.ActiveProCri.style.boxShadow = "0px 0px  20px 7px rgb(255,255,0)"
                    }
                    
                }
            })
            
        }
    }



    // ajax
    componentWillUnmount(){
        $("body").css("overflow","visible")
        clearTimeout(this.timerItem1)
        clearTimeout(this.timerItem2)
        if(this.getUser){
            this.getUser.abort()
            console.log('1')
        }
        else if(this.getUserItem){
            this.getUserItem.abort()
            console.log('2')
        }
        else if(this.userAssisted){
            this.userAssisted.abort()
            console.log('3')
        }
        else if(this.receiveGiftOrder){
            this.receiveGiftOrder.abort()
            console.log('4')
        }   
    }



    // 
    Act(){
        var key = window.location.hash.substr(9)
        // console.log(key);
        var storage = window.localStorage
        var be_mid = storage.be_mid
        var _this = this
        
        // console.log(this.state.isAct);
        

        // 
        if(this.state.isAct){
            // console.log('')
            this.setState({
                isAct : false
            })


            // ajax
            this.userAssisted = $.get(userAssisted , {be_mid : be_mid , keys : key} , function(res){
                console.log(res);
                // console.log(be_mid)
                // console.log(key)
                if(res.msg === ""){
                    console.log('success');
                    _this.refs.ActSucWrap.style.display = "block"
                    _this.refs.ActSuccessWrap.style.display = "block"
                    _this.refs.ActBtn.innerHTML = ""

                    if(_this.state.isCount === 0){
                        _this.refs.ActiveProCri.style.boxShadow = "0px 0px  20px 7px rgb(255,255,0)"
                        _this.setState({
                            isCount : 1
                        })
                    }
                    else if(_this.state.isCount === 1){
                        _this.refs.ActiveProCri.style.boxShadow = "0px 0px  20px 7px rgb(255,255,0)"
                        _this.setState({
                            isCount : 2
                        })
                    }
                    else{
                        _this.setState({
                            isCount : 2
                        })
                    }
                }
                else{
                    _this.props.history.push('/Login')
                }
            })
        }

        // 
        else{
            // console.log('')
            this.refs.ActBtn.innerHTML = ""
            this.props.history.push('/')
        }
    }


    // 
    SucAct(){
        // console.log('');
        var storage = window.localStorage
        var mid = storage.mid
        var _this = this


        // ajax
        this.receiveGiftOrder = $.ajax({
            url: receiveGiftOrder,
            data : { mid : mid,
                     name : ActiveName,
                     type : 0
                    },
            type: "get",
            dataType: 'json',
            contentType: "application/x-www-form-urlencoded",
        }).done(function(res){
            console.log(res.msg)
            if(res.msg === ""){
                console.log('')
                _this.props.history.push('/')
            }
            else{
                console.log('')
                _this.props.history.push('/ActSuc')
            }
        })
    }


    // 
    close(e){
        $("body").css("overflow","visible")
        // console.log('object');
        this.setState({
            liked : false
        })
    }



    // 
    closeCode(e){
        // console.log('object');
        $("body").css("overflow","visible")
        this.refs.codeWrap.style.display = "none"
        this.refs.code.style.display="none"
        this.refs.SaveImgBtn.style.display="none"
    }

    // 
    nocloseCode(){
        return false
    }

    // 
    closeActSuc(){
        // console.log('ss')
        this.refs.ActSucWrap.style.display = "none"
        this.refs.ActSuccessWrap.style.display = "none"
    }

    // 
    saveImg(e){
        // console.log(window.location.href)
        $("body,html").scrollTop(0);
        $("body").css("overflow","hidden")
        e.preventDefault()
        this.setState({
            liked:true
        })
    }

    // 
    SaveImgBtn(e){
        // console.log('ss')
        
        this.refs.childRefTwo.refs.Tips.style.top = '0%'
         
        // console.log(this.refs.code);
        
        var _this = this
        setTimeout(function(){
            _this.refs.childRefTwo.refs.Tips.style.top = '-40%'
        },2000)
        this.setState({
            TipsInfo : '!'
        })
    }

    
    // 
    creactImg(e){
        var mycansrc= $('#qrcode canvas')[0]; 
        var canvas =this.refs.creactCanvas;

        $("body,html").scrollTop(0);
        $("body").css("overflow","hidden")

        // baseData:""  /base64
        var _this = this

        this.setState({
            isClose : true
        })
        
        this.refs.codeWrap.style.display = "block"
        // console.log(this.refs.childRef.refs.Loading);
        this.refs.childRef.refs.Loading.style.display = "block"
        this.refs.SaveImgBtn.style.display = "block"
        

         getBase64(mycansrc,canvas,urlImg);

         setTimeout(function(){
            _this.refs.code.style.display = 'block'
            var baseData =   sessionStorage.getItem("baseData");
            _this.refs.childRef.refs.Loading.style.display = "none"
            _this.refs.Closecode.style.display = "block"
            // console.log(baseData)
            _this.setState({
                isClose : false,
                baseData:baseData
            })
         },5000)
        
       
       

    }
    



    render(){
        return(
            <div className="ActiveWrap">
                <div className="Active">
                    <Tips 
                        ref="childRefTwo"
                        info={this.state.TipsInfo}
                    ></Tips>
                    
                    {/** */}
                    <header className="ActHeaderWrap">
                        <div className="ActHeader">
                            <img src={ActTit} width="100%" alt=""/>
                        </div>
                    </header>

                    {/** */}
                    <div className="ActUserWrap">
                        <img src={ActUser} width="100%"/>
                        <div className="ActUser">
                            <span className="ActUserImg" ref="userImg">
                                
                            </span>
                            <span className="ActUserId">
                                {this.state.userName}
                            </span>
                        </div>
                    </div>

                    {/** */}
                    <div className="ActiveImg">
                        <div className="ActiveImgWrap">
                            
                        </div>
                        {/** <QRcode size={150} value={this.state.url} style={{marginLeft : `${115}px`}}/> */}
                        <div className="ImgWrap">
                            <div className="Img">
                                <span></span>
                                <span></span>
                                <span></span>
                                <span></span>

                                <div className="ImgMain"></div>

                            </div>
                        </div>
                    </div>

                    {/**  */}
                    <div className="ActivePro">
                        <div className="ActivePro-title">
                            <span>
                                {this.state.ActProTil ? "2," : "2,"}
                            </span>
                        </div>
                        <div className="ActivePro-main">
                            <span>
                                <span className="finish" style={ this.state.isCount === 0 ? {'width':`${0}%`} : this.state.isCount === 1 ? {'width' : `${50}%`} : {'width' : `${100}%`} }></span>
                            </span>
                        <span className="ActiveProOne">
                            <span className="ActiveProCri" ref="ActiveProCri" style={ this.state.isCount >= 1 ? {background: `yellow`} : {background: `white`}}></span>
                            <span className="ActiveProOP">1</span>
                        </span>
                        <span className="ActiveProTwo">
                                <span className="ActiveProCri1"></span>
                            <span className="ActiveProOP">2</span>
                        </span>
                        </div>
                    </div>

                    {/***/}
                    <div className="ActPayBck" ref="ActPayBck">
                        <div className="ActPayWrap">
                            <div className="ActPay" ref="ActPay">
                                <Link to="/Postage">
                                    <span>,</span>
                                </Link>
                            </div>
                        </div>
                    </div>
                    

                    {/** */}
                    <footer className="ActBtnWrap">
                        <div className="ActBtn">
                            {/**<img src={ActBtn} width="100%"/>*/}
                                <span className="ActB"  
                                    onClick = {this.state.isYou ? this.Act.bind(this) : this.state.isMe ? this.saveImg.bind(this) : this.SucAct.bind(this)}
                                >
                                    <p ref="ActBtn">{
                                        // this.state.isMe ? "" : ""
                                        this.state.isYou ? "" : this.state.isMe ? "" : ""
                                    }</p>
                                </span>
                            
                        </div>
                    </footer>

                    {/**  */}
                    <div className="imgWrap" onClick={this.close.bind(this)} ref="saveImg" style={{'display' : this.state.liked == true ? 'block' : 'none'}}>
                        
                    </div>
                    {/** */}
                    <div className="saveImgWrap" style={{'display' : this.state.liked == true ? 'block' : 'none'}}>
                        <div className="saveImg" onClick={this.creactImg.bind(this)}>
                            <img src={saveImg} width="14%" />
                            <span></span>
                        </div>
                    </div>

                    {/**  */}
                    <div className="codeWrap" ref="codeWrap"  ></div>
                    {/** */}

                    <div className="saveCode_Wrap">
                        <div className="saveCodeWrap" ref="code">
                            <div className="closeCode" ref="Closecode">
                                <span onClick={this.state.isClose ? this.nocloseCode.bind(this) : this.closeCode.bind(this)}></span>
                            </div>
                            <div id="qrcode" style={{"display":"none"}}>
                                <QRcode value={this.state.url}></QRcode>
                            </div>
                            <img className="codeImg" src={this.state.baseData} style={{"display":this.state.baseData===""?"none":"block"}}/>
                            <canvas id="canvas" ref="creactCanvas" style={{"display":"none"}}>
                            </canvas>
                        </div>
                    </div>

                    

                    {/**  */}
                    <div className="SaveImgBtnWrap" ref="SaveImgBtn" >
                        <div className="SaveImgBtn" onClick={this.SaveImgBtn.bind(this)}>
                            <span></span>
                        </div>
                    </div>
                    <Loading ref="childRef"></Loading>
                    

                    {/**  */}
                    <div className="ActSucWrap" ref="ActSucWrap" onClick = {this.closeActSuc.bind(this)}></div> {/**  */}
                    
                    <div className="ActSuccessWrap" ref="ActSuccessWrap">
                        <div className="ActSuccess_Wrap">
                            <Link to="/">
                                <span className="ActSuccess_btn">
                                    
                                </span>     
                            </Link>
                            
                            <img src={Act_Suc} width="100%" />
                        </div>
                        <div className="CloseActSuccess" > 
                            <span onClick={this.closeActSuc.bind(this)}></span>        
                        </div>
                        {/**<div className="CloseActSuccess">
                            <span className="CloseActSuccess_btn" onClick={this.closeActSuc.bind(this)}></span>
                            <img src={Act_Suc_Close} width="100%"/>
                                </div>*/}
                    </div> {/**  */}
                </div>
                
            </div>
        )
    }
}
export default Active