import React from 'react'
import { Link } from 'react-router-dom'
import './Postage.scss'
import $ from 'jquery'
// import User from './../Components/User/User'

// 
import posUser from './images/ActiveUser.png'
import posPayItem1 from './images/posPayItem1.png'
import posPayItem2 from './images/posPayItem2.png'

// 
import Pay from './../Pay/Pay'

// 
import {receiveGiftOrder , checkReceive} from './../apis/api'



class Postage extends React.Component{
    constructor(props){
        super(props)
        this.state={
            userName : " ",
            userImg : ''
        }
    }
    componentDidMount(){
        console.log(this.props)
            var Storage = window.localStorage
            var mid = Storage.mid
            var _this = this
            this.setState({
                userName : Storage.nickname
            })
            this.refs.userImg.style.background = "url(" + Storage.avatar + ")"
            this.refs.userImg.style.backgroundSize = "100% 100%"

            this.checkRec = $.get(checkReceive , {mid:mid},function(res){
                if(res.data){
                    // _this.props.history.push('/')
                    // console.log(_this.props)
                }
                else{
                    console.log('meiyoulingquguo');
                }
            })
        

    }
    openPay(){
        // console.log(this.refs.childRef.refs.Pay);
        this.refs.childRef.refs.Pay.style.display = 'block'
    }
    render(){
        return(
            <div className="posWrap">

                {/**  */}
                <header className="posHeaderWrap">
                    <div className="posHeader">
                        <span className="posTil">
                            !
                        </span>
                    </div>
                </header>

                {/** */}
                <div className="posUserWrap">
                    <img src={posUser} width="100%"/>
                    <div className="posUser">
                        <span className="posUserImg" ref="userImg">
                            
                        </span>
                        <span className="posUserId">
                            {this.state.userName}
                        </span>
                    </div>
                </div>
                
                {/** */}
                <div className="posPayWrap">
                    <div className="posPay">
                        <h1 style={{fontSize : `${1}rem`}}>:8</h1>

                        <div className="posPayItem1">
                            <img src={posPayItem1} width="100%"/>
                            
                                <span className="posPayItemBtn" onClick={this.openPay.bind(this)}></span>
                            
                            
                        </div>
                        <div className="posPayItem2">
                            <img src={posPayItem2} width="100%"/>

                            <Link to="/Active">
                                <span className="posPayItemBtn" ></span>
                            </Link>
                                
                           
                            
                        </div>
                    </div>
                </div>
            
                <Pay ref="childRef"></Pay>
            </div>
        )
    }
}
export default Postage 